export default {
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		modelValue: {
			type: [String, Number],
			default: ''
		},
		// 
		placeholder: {
			type: [String, Number],
			default: ''
		},
		// placeholderstylescoped/deep/
		placeholderClass: {
			type: String,
			default: 'textarea-placeholder'
		},
		// placeholder
		placeholderStyle: {
			type: [String, Object],
			default: 'color: #c0c4cc'
		},
		// 
		height: {
			type: [String, Number],
			default: 70
		},
		// App-vueH5
		confirmType: {
			type: String,
			default: 'return'
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		count: {
			type: Boolean,
			default: false
		},
		// nvueH5
		focus: {
			type: Boolean,
			default: false
		},
		// 
		autoHeight: {
			type: Boolean,
			default: false
		},
		// textareaposition:fixedfixedtrue
		fixed: {
			type: Boolean,
			default: false
		},
		// 
		cursorSpacing: {
			type: Number,
			default: 0
		},
		// focus
		cursor: {
			type: [String, Number],
			default: ''
		},
		// 
		showConfirmBar: {
			type: Boolean,
			default: true
		},
		// selection-end
		selectionStart: {
			type: Number,
			default: -1
		},
		// selection-start
		selectionEnd: {
			type: Number,
			default: -1
		},
		// 
		adjustPosition: {
			type: Boolean,
			default: true
		},
		//  iOS 
		disableDefaultPadding: {
			type: Boolean,
			default: false
		},
		// focus
		holdKeyboard: {
			type: Boolean,
			default: false
		},
		//  -1 
		maxlength: {
			type: [String, Number],
			default: 140
		},
		// surround-bottom-
		border: {
			type: String,
			default: 'surround'
		},
		// 
		formatter: {
			type: [Function, null],
			default: null
		},
		// 
		ignoreCompositionEvent: {
			type: Boolean,
			default: true
		},
		// 
		confirmHold: {
			type: Boolean,
			default: false
		},
		// 
		textStyle: {
			type: [Object, String],
			default: () => {}
		},
		// 
		countStyle: {
			type: [Object, String],
			default: () => {}
		},
		...uni.$uv?.props?.textarea
	}
}