export default {
	props: {
		value: {
			type: [Boolean, String, Number],
			default: false
		},
		modelValue: {
			type: [Boolean, String, Number],
			default: false
		},
		// 
		loading: {
			type: Boolean,
			default: false
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// px
		size: {
			type: [String, Number],
			default: 25
		},
		// 
		activeColor: {
			type: String,
			default: '#2979ff'
		},
		// 
		inactiveColor: {
			type: String,
			default: '#fff'
		},
		// switch
		activeValue: {
			type: [String, Number, Boolean],
			default: true
		},
		// switch
		inactiveValue: {
			type: [String, Number, Boolean],
			default: false
		},
		// 
		asyncChange: {
			type: Boolean,
			default: false
		},
		// 
		space: {
			type: [String, Number],
			default: 0
		},
		...uni.$uv?.props?.switch
	}
}