export default {
	props: {
		// infoprimarysuccesswarningerror
		type: {
			type: String,
			default: 'primary'
		},
		// 
		disabled: {
			type: [Boolean, String],
			default: false
		},
		// largemediummini
		size: {
			type: String,
			default: 'medium'
		},
		// tagcircle, square
		shape: {
			type: String,
			default: 'square'
		},
		// 
		text: {
			type: [String, Number],
			default: ''
		},
		// 
		bgColor: {
			type: String,
			default: ''
		},
		// 
		color: {
			type: String,
			default: ''
		},
		// 
		borderColor: {
			type: String,
			default: ''
		},
		// 
		name: {
			type: [String, Number],
			default: ''
		},
		// 
		plainFill: {
			type: Boolean,
			default: false
		},
		// 
		plain: {
			type: Boolean,
			default: false
		},
		// 
		closable: {
			type: Boolean,
			default: false
		},
		// 
		closeColor: {
			type: String,
			default: '#C6C7CB'
		},
		//  rightright-top right-top
		closePlace: {
			type: String,
			default: 'right-top'
		},
		// 
		show: {
			type: Boolean,
			default: true
		},
		// 
		icon: {
			type: String,
			default: ''
		},
		// 
		iconColor: {
			type: String,
			default: ''
		},
		// nvue uv-listcell
		cellChild: {
			type: Boolean,
			default: false
		},
		...uni.$uv?.props?.tags
	}
}