export default {
	props: {
		// 
		loadingText: {
			type: [String, Number],
			default: ''
		},
		// loading
		image: {
			type: String,
			default: ''
		},
		// circle-spinner-semicircle-
		loadingMode: {
			type: String,
			default: 'circle'
		},
		// 
		loading: {
			type: Boolean,
			default: false
		},
		// 
		bgColor: {
			type: String,
			default: '#fff'
		},
		// 
		color: {
			type: String,
			default: '#C8C8C8'
		},
		// 
		fontSize: {
			type: [String, Number],
			default: 16
		},
		// 
		iconSize: {
			type: [String, Number],
			default: 26
		},
		// rgb
		loadingColor: {
			type: String,
			default: '#C8C8C8'
		},
		// 
		duration: {
			type: [String, Number],
			default: 300
		},
		...uni.$uv?.props?.loadingPage
	}
}