export default {
	props: {
		// ms
		duration: {
			type: Number,
			default: 300
		},
		// tabs
		list: {
			type: Array,
			default: () => []
		},
		// 
		lineColor: {
			type: String,
			default: '#3c9cff'
		},
		// 
		activeStyle: {
			type: [String, Object],
			default: () => ({
				color: '#303133'
			})
		},
		// 
		inactiveStyle: {
			type: [String, Object],
			default: () => ({
				color: '#606266'
			})
		},
		// 
		lineWidth: {
			type: [String, Number],
			default: 20
		},
		// 
		lineHeight: {
			type: [String, Number],
			default: 3
		},
		// 
		lineBgSize: {
			type: String,
			default: 'cover'
		},
		// item
		itemStyle: {
			type: [String, Object],
			default: () => ({
				height: '44px'
			})
		},
		// 
		scrollable: {
			type: Boolean,
			default: true
		},
		// 
		current: {
			type: [Number, String],
			default: 0
		},
		// 
		keyName: {
			type: String,
			default: 'name'
		},
		...uni.$uv?.props?.tabs
	}
}