export default {
	props: {
		// loadmore-loading-nomore-
		status: {
			type: String,
			default: 'loadmore'
		},
		// 
		bgColor: {
			type: String,
			default: 'transparent'
		},
		// 
		icon: {
			type: Boolean,
			default: true
		},
		// 
		fontSize: {
			type: [String, Number],
			default: 14
		},
		// 
		iconSize: {
			type: [String, Number],
			default: 16
		},
		// 
		color: {
			type: String,
			default: '#606266'
		},
		// spinner-circle-semicircle-
		loadingIcon: {
			type: String,
			default: 'spinner'
		},
		// 
		loadmoreText: {
			type: String,
			default: ''
		},
		// 
		loadingText: {
			type: String,
			default: '...'
		},
		// 
		nomoreText: {
			type: String,
			default: ''
		},
		// 
		isDot: {
			type: Boolean,
			default: false
		},
		// 
		iconColor: {
			type: String,
			default: '#b7b7b7'
		},
		// 
		marginTop: {
			type: [String, Number],
			default: 10
		},
		// 
		marginBottom: {
			type: [String, Number],
			default: 10
		},
		// px
		height: {
			type: [String, Number],
			default: 'auto'
		},
		// 
		line: {
			type: Boolean,
			default: false
		},
		// 
		lineColor: {
			type: String,
			default: '#E6E8EB'
		},
		// true-false-
		dashed: {
			type: Boolean,
			default: false
		},
		...uni.$uv?.props?.loadmore
	}
}